/* Default (disconnected) style for the ConnectButton */
.my-connect-button {
  background-color: #000;
  color: #fff;
  border-radius: 5px;
  padding: 10px 20px;
  cursor: pointer;
  border: none;
}

/* Hover style */
.my-connect-button:hover {
  background-color: #333;
}

/* Connected style override */
.my-connect-button.connected {
  background-color: transparent;
  color: #fff;
}
